"use client";

import Image from "next/image";
import Link from "next/link";
import { useEffect, useMemo, useRef, useState } from "react";

import { useCart } from "@/context/CartContext";
import { useLanguage } from "@/context/LanguageContext";
import {
  getCategoriesWithSubcategories,
  getProductsByIds,
  getVariantsByIds,
  type ProductRow,
  type VariantRow,
} from "@/lib/db";

const HERO_SLIDES = [
  { id: 1, img: "/hero1.webp" },
  { id: 2, img: "/hero2.webp" },
  { id: 3, img: "/hero3.webp" },
];

function money(n: number) {
  return `₹${Number(n ?? 0).toFixed(0)}`;
}

function PrimarySecondary({
  primary,
  secondary,
  center = true,
  primaryClass = "text-[11px] font-medium",
  secondaryClass = "text-[9px] text-gray-500",
}: {
  primary: string;
  secondary?: string;
  center?: boolean;
  primaryClass?: string;
  secondaryClass?: string;
}) {
  return (
    <div className={`${center ? "text-center" : "text-left"} leading-tight`}>
      <div className={primaryClass}>{primary}</div>
      {secondary ? <div className={secondaryClass}>{secondary}</div> : null}
    </div>
  );
}

type CartItem = { productId: number; variantId?: number | null; qty: number };

export default function HomePage() {
  const sectionRefs = useRef<Record<string, HTMLDivElement | null>>({});
  const [activeSlide, setActiveSlide] = useState(0);

  const { items } = useCart();
  const { lang } = useLanguage();

  const [categoryMap, setCategoryMap] = useState<any[]>([]);
  const [loadingCats, setLoadingCats] = useState(true);

  // Lookup maps for cart totals
  const [productMap, setProductMap] = useState<Record<number, ProductRow>>({});
  const [variantMap, setVariantMap] = useState<Record<number, VariantRow>>({});

  useEffect(() => {
    const t = setInterval(
      () => setActiveSlide((s) => (s + 1) % HERO_SLIDES.length),
      3500
    );
    return () => clearInterval(t);
  }, []);

  useEffect(() => {
    let alive = true;
    (async () => {
      try {
        setLoadingCats(true);
        const cats = await getCategoriesWithSubcategories();
        if (!alive) return;
        setCategoryMap(cats);
      } catch (e: any) {
        console.error("HOME LOAD CATEGORIES ERROR:", e);
      } finally {
        if (alive) setLoadingCats(false);
      }
    })();
    return () => {
      alive = false;
    };
  }, []);

  // Fetch ONLY what's needed to price items in cart
  useEffect(() => {
    let alive = true;

    (async () => {
      const cart = (Array.isArray(items) ? (items as any) : []) as CartItem[];
      const pids = cart.map((x) => x.productId);
      const vids = cart.map((x) => x.variantId).filter((v): v is number => v != null);

      try {
        const [ps, vs] = await Promise.all([getProductsByIds(pids), getVariantsByIds(vids)]);
        if (!alive) return;

        const pm: Record<number, ProductRow> = {};
        for (const p of ps) pm[p.id] = p;

        const vm: Record<number, VariantRow> = {};
        for (const v of vs) vm[v.id] = v;

        setProductMap(pm);
        setVariantMap(vm);
      } catch (e) {
        console.error(e);
      }
    })();

    return () => {
      alive = false;
    };
  }, [items]);

  const scrollToCategory = (slug: string) => {
    const el = sectionRefs.current[slug];
    el?.scrollIntoView({ behavior: "smooth", block: "start" });
  };

  const cartTotals = useMemo(() => {
    const cart = (Array.isArray(items) ? (items as any) : []) as CartItem[];

    let total = 0;
    let count = 0;

    for (const it of cart) {
      const p = productMap[it.productId];
      if (!p) continue;

      const v = it.variantId != null ? variantMap[it.variantId] : null;
      const price = Number(v?.price ?? p.base_price ?? 0);

      total += price * (it.qty ?? 1);
      count += it.qty ?? 1;
    }

    return { total, count };
  }, [items, productMap, variantMap]);

  const cartCtaPrimary = lang === "en" ? "Go to Cart →" : "U gudub Gaadhiga →";
  const cartCtaSecondary = lang === "en" ? "U gudub Gaadhiga" : "Go to Cart";

  return (
    <main className="min-h-screen bg-white text-black">
      {/* TOP CATEGORY STRIP */}
      <section className="border-b bg-white">
        <div className="mx-auto max-w-md px-2 py-2 flex gap-4 overflow-x-auto">
          {loadingCats ? (
            <div className="text-sm text-gray-500 px-2 py-3">Loading…</div>
          ) : (
            categoryMap.map((cat: any) => {
              const primary = lang === "en" ? cat.name_en : cat.name_so;
              const secondary = lang === "en" ? cat.name_so : cat.name_en;

              return (
                <button
                  key={cat.id}
                  onClick={() => scrollToCategory(cat.slug)}
                  className="min-w-[78px]"
                >
                  <div className="mx-auto h-14 w-14 rounded-full bg-blue-50 overflow-hidden flex items-center justify-center">
                    <Image
                      src={
                        typeof cat.img === "string" && cat.img.trim().length > 0
                          ? cat.img.trimEnd()
                          : "/example.png"
                      }
                      alt={primary}
                      width={56}
                      height={56}
                      className="w-full h-full object-contain p-1"
                    />
                  </div>

                  <div className="mt-1">
                    <PrimarySecondary primary={primary} secondary={secondary} />
                  </div>
                </button>
              );
            })
          )}
        </div>
      </section>

      {/* HERO */}
      <section className="bg-white px-4 pt-3">
        <div className="relative overflow-hidden rounded-2xl h-[180px]">
          <div
            className="flex transition-transform duration-500"
            style={{ transform: `translateX(-${activeSlide * 100}%)` }}
          >
            {HERO_SLIDES.map((s) => (
              <Image
                key={s.id}
                src={s.img}
                alt="promo"
                width={400}
                height={180}
className="flex-none w-full h-[180px] object-contain bg-white"              />
            ))}
          </div>
        </div>
      </section>

      {/* CATEGORY SECTIONS */}
      <section className="bg-white px-3 pb-10 pt-3">
        {categoryMap.map((cat: any) => {
          const catPrimary = lang === "en" ? cat.name_en : cat.name_so;
          const catSecondary = lang === "en" ? cat.name_so : cat.name_en;

          return (
            <div
              key={cat.id}
              ref={(el) => {
                sectionRefs.current[cat.slug] = el;
              }}
              className="scroll-mt-[140px] pb-6"
            >
              <div className="mb-3">
                <PrimarySecondary
                  primary={catPrimary}
                  secondary={catSecondary}
                  center={false}
                  primaryClass="text-xl font-extrabold"
                  secondaryClass="text-xs text-gray-500"
                />
              </div>

              <div className="grid grid-cols-4 gap-x-4 gap-y-6">
                {(cat.subcats || []).slice(0, 8).map((sub: any) => {
                  const subPrimary = lang === "en" ? sub.name_en : sub.name_so;
                  const subSecondary = lang === "en" ? sub.name_so : sub.name_en;

                  return (
                    <Link
                      key={sub.id}
                      href={`/subcategory/${sub.slug}`}
                      className="flex flex-col items-center"
                    >
                      <div className="relative h-[68px] w-[68px] rounded-xl overflow-hidden bg-gray-100">
                        <Image
                          src={
                            typeof sub.img === "string" && sub.img.trim().length > 0
                              ? sub.img.trimEnd()
                              : "/example.png"
                          }
                          alt={subPrimary}
                          fill
                          className="object-cover"
                        />
                      </div>

                      <div className="mt-1">
                        <PrimarySecondary primary={subPrimary} secondary={subSecondary} />
                      </div>
                    </Link>
                  );
                })}
              </div>
            </div>
          );
        })}
      </section>

      {/* STICKY GO TO CART BAR */}
      {cartTotals.count > 0 && (
        <div className="fixed left-0 right-0 bottom-16 z-40">
          <div className="mx-auto max-w-md px-3">
            <Link
              href="/cart"
              className="flex items-center justify-between bg-[#0B6EA9] text-white rounded-2xl px-4 py-3 shadow-lg"
            >
              <div>
                <div className="text-xs opacity-90">
                  {cartTotals.count} item{cartTotals.count > 1 ? "s" : ""} in cart
                </div>
                <div className="text-lg font-extrabold">{money(cartTotals.total)}</div>
              </div>

              <div className="text-right leading-tight font-extrabold">
                <div>{cartCtaPrimary}</div>
                <div className="text-[10px] opacity-80">{cartCtaSecondary}</div>
              </div>
            </Link>
          </div>
        </div>
      )}
    </main>
  );
}
